<template>
    <el-form :class="form-content" ref="form" @submit.prevent="onSubmit" v-model="form" label-width="80px">
        <el-form-item label="活动名称">
            <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="活动区域">
            <el-select v-model="form.region" placeholder="请选择活动区域">
                <el-option value="shanghai" label="区域一"></el-option>
                <el-option value="beijing" label="区域二"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="活动时间">
            <el-col :span="11">
                <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
            </el-col>
            <el-col :span="2">-</el-col>
            <el-col :span="11">
                <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>

            </el-col>
        </el-form-item>
        <el-form-item label="即时配送">
            <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="活动性质">
            <el-checkbox-group v-model="form.type">
                <el-checkbox label="地铁" name="type"></el-checkbox>
                <el-checkbox label="海上" name="type"></el-checkbox>
                <el-checkbox label="大楼" name="type"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="特殊资源">
            <el-radio-group v-model="form.resource">
                <el-radio label="线上资源"></el-radio>
                <el-radio label="线下免费"></el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item label="活动形式">
            <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="create">立即创建</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import '../../style/form.css'
    export default{
		data(){
            return{
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
            }
        },
        methods:{
			onSubmit:function(){
				console.log('sdfj')
            },
			create:function(){
				console.log(this.form.name);
				console.log(this.form.region);
				console.log(this.form.date1);
				console.log(this.form.date2);
				console.log(this.form.delivery);
				console.log(this.form.type);
				console.log(this.form.resource);
				console.log(this.form.desc);
            }
        }
    }
</script>


